.wrap {
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  min-height: 100%;
  z-index: 100;

  &.light_theme {
    .spinner {
      svg {
        path {
          stroke: #007acc;
        }
      }
    }
  }

  &.dark_theme {
    .spinner {
      svg {
        path {
          stroke: #FBD38D;
        }
      }
    }
  }
}


.spinner {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, 30%);
  z-index: 100;
  width: 200px;
  height: 200px;
}


.spinner svg {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.center {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;


  .spinner {
    transform: translate(-50%, -50%);
  }
}